.container {
    width: 57%
}

.banner {
    background-image: url(https://hexschool.github.io/webLayoutTraining1st/week3/qa.png);
    background-position: center;
    background-repeat: no-repeat;
    height: 304px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 16px;
}

.banner__content {
    display: flex;
}

.banner__detail {
    font-size: 64px;
    line-height: 80px;
}

.banner__icon {
    max-width: 100%;
    display: block;
}

.wrap {
    display: flex;
    justify-content: center;
}

.location {
    margin-bottom: 32px;
}

.location__label {
    font-size: 24px;
    line-height: 30px;
    margin-right: 20px;
}

.location__select {
    width: 324px;
    font-size: 18px;
    line-height: 24px;
    padding: 10px;
}

.locationContent {
    display: flex;
    margin-bottom: 300px;
}

.locationContent__detail {
    width: 445px;
    margin-right: 30px;
}

.locationDetail__image {
    display: block;
    width: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.locationDetail__title {
    font-size: 24px;
    line-height: 36px;
    padding: 16px;
    border: 1px solid #00000020;
}

.locationDetail_description{
    border: 1px solid #00000020;
}

.locationDetail__item{
    display: flex;
    margin: 8px;
    font-size: 20px;
    line-height: 30px;
}

.locationItem__incon {
    display: inline-block;
    width: 21px;
    height: 21px;
    margin-right: 12px;
}